





import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Select extends Vue {
  httpMethods: any[] = [
    {
      label: 'GET',
      value: 'GET',
    },
    {
      label: 'POST',
      value: 'POST',
    },
    {
      label: 'PUT',
      value: 'PUT',
    },
    {
      label: 'DELETE',
      value: 'DELETE',
    },
    {
      label: 'INFO',
      value: 'INFO',
    },
    {
      label: 'DUMB',
      value: 'DUMB',
    },
  ];

  created() {
    this.$emit('selected', this.httpMethods[0].value);
  }
}
